footer {
    background: #333;
    color: #fff;
    text-align: center;
    padding: 20px 0;
    font-weight: 400;
    font-size: 12px;
    position: relative;
    z-index: 2;
    box-shadow:0 1px 10px rgba(0, 0, 0, 0.3);
    p:last-child {
        margin-bottom: 0;
        font-size: 11px;
    }
    a {
        color: #F95700;
    }
}
.js--contrast {
    footer {
        background:#fff;
        color: #333;
    }
}